.board {
    background-color: rgb(68, 68, 68);
    text-align: center;
    justify-content: center;
    width: min-content;
    margin: auto;
    padding: 10px;
    border-radius: 6px;
    border-style: solid;
    border-color: rgb(121, 121, 121);
    border-width: 1px;
}
.board-row {
    display: flex;
    align-items: center;
    justify-content: center;
}
.square {
    border-style: solid;
    border-color: rgb(54, 22, 0);
    border-width: 1px;
    height: 60px;
    width: 60px;
    text-align: center;
    color: rgb(0, 0, 0);
}
.square button {
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,0);
    border: none;
    color: rgb(0, 204, 0);
}
.square button:focus {
    outline-style: solid;
    outline-color: white;
    outline-width: 2px;
}
.squares {
    border-style: solid;
    border-color: rgb(17, 17, 17);
    border-width: 1px;
}
.waiting {
    width: 500px;
    margin: auto;
    background-color: rgb(68, 68, 68);
    margin-bottom:  3vh;
    padding: 20px;
    padding-bottom: 30px;
    border-radius: 8px;
    border-style: solid;
    border-color: rgb(165, 165, 165);
    border-width: 1px; 
    color: white !important;
}
.pawn-dialog-item {
    height: 60px;
    width: 60px;
    margin: auto;
    padding: auto;
}
.pawn-dialog-but {
    width: 100%;
    height: 100%;
}
.pawn-dialog-but:hover {
    background-color: rgb(54, 54, 54) !important;
}